html,
body,
div,
span,
applet,
object,
iframe,
h1,
h2,
h3,
h4,
h5,
h6,
p,
blockquote,
pre,
a,
abbr,
acronym,
address,
big,
cite,
code,
del,
dfn,
em,
img,
ins,
kbd,
q,
s,
samp,
small,
strike,
strong,
sub,
sup,
tt,
var,
b,
u,
i,
center,
dl,
dt,
dd,
ol,
ul,
li,
fieldset,
form,
label,
legend,
table,
caption,
tbody,
tfoot,
thead,
tr,
th,
td,
article,
aside,
canvas,
details,
embed,
figure,
figcaption,
footer,
header,
hgroup,
menu,
nav,
output,
ruby,
section,
summary,
time,
mark,
audio,
video {
	margin: 0;
	padding: 0;
	border: 0;
	vertical-align: baseline;
}

ol,
ul {
	list-style: none;
}

table {
	border-collapse: collapse;
	border-spacing: 0;
}

caption,
th,
td {
	text-align: left;
	vertical-align: middle;
}

q,
blockquote {
	quotes: none;
}

q {
	&:before,
	&:after {
		content: "";
		content: none;
	}
}

blockquote {
	&:before,
	&:after {
		content: "";
		content: none;
	}
}

a {
	img {
		border: none;
		outline: none;
	}

	outline: none;
	text-decoration: none;
}

article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
main,
menu,
nav,
section,
summary {
	display: block;
}

*,
:before,
:after {
	box-sizing: border-box;
}

html {
	width: 100%;
	height: 100%;
	min-height: 100%;
	position: relative;
	overflow-x: hidden;
}

body {
	width: 100%;
	height: 100%;
	min-height: 100%;
	position: relative;
	overflow-x: hidden;
	-webkit-backface-visibility: hidden;
	backface-visibility: hidden;
}

a {
	outline: none;
	text-decoration: none;
	color: #fff;
}

img {
	vertical-align: middle;
}

input:focus,
select:focus,
textarea:focus,
button:focus {
	outline: none;
}

/*did this body auto height to make fancy box work properly */

body {
	height: auto;
}

.container {
	width: 100%;
	height: auto;
	color: white;
	position: relative;
}

.innerContainer {
	max-width: 1440px;
	margin: 0 auto;
	position: relative;
	width: 95%;

	/*	z-index: 1;*/
}

.isHover {
	transition: opacity 0.3s ease-in-out;
	-webkit-transition: opacity 0.3s ease-in-out;
	-moz-transition: opacity 0.3s ease-in-out;
	-ms-transition: opacity 0.3s ease-in-out;

	&:hover {
		cursor: pointer;
		opacity: 0.8;
	}
}
