#story {
	position: relative;
	padding: 1em;
	color: #fff;
	display: flex;
	text-align: justify;
	flex-wrap: wrap;
	justify-content: center;
	font-family: "Tsukimi Rounded Medium", sans-serif;
	font-style: normal;
	margin-left: auto;
	margin-right: auto;
	max-width: 1600px;
	
	font-size: clamp(13px, 2vw, 40px);

	&::before {
		content: "";
		background-attachment: fixed;
		background-size: cover;
		z-index: -2;
	}

	&::after {
		content: "";

		/*
		background: 
		linear-gradient(to bottom, rgba(0,0,0,1) 0%, rgba(0,0,0,0) 15%),
		linear-gradient(to top, rgba(0,0,0,1) 0%, rgba(0,0,0,0) 15%);
		*/
		z-index: -1;
	}

	&::before,
	&::after {
		background-position: center;
		background-repeat: no-repeat;
		width: 100%;
		height: 100%;
		position: absolute;
		left: 0;
		top: 0;
		right: 0;
		margin: auto;
	}

	.content-row {
		display: grid;
		grid-template-columns: repeat(12, 1fr);
	}
}

#story.section-header {
	width: 100%;
}

/*
#story h2 {
	font-size: 1.5em;
	letter-spacing: .1em;
}
*/

#story h5 {
	font-family: "Tsukimi Rounded Light", sans-serif;;
	color: rgba(216, 209, 204, 0.4);
}

.feature-text-col {
	letter-spacing: 0.05em;

	h2 {
		text-align: left;
	}
}

#story {
	ul li::before {
		content: "• ";
	}

	li {
		margin-bottom: 0.5em;
	}

	.text-block {
		background-color: #0d0e56;
		padding: clamp(50px, 5vw, 100px);

		/* padding: 2em; */
		max-width: 100%;
		border: 5px solid #05fdfd;
		z-index: -1;
		position: relative;
	}
}

.storyrow {
	margin-top: 150px;
	margin-bottom: 5%;
}

.textbox-header {
	font-family: ivypresto-display, serif;
	font-style: normal;
	color: #05fdfd;
	font-size: clamp(25px, 5vw, 125px);
	position: relative;
	z-index: 1;
	top: 0px;

	&::after {
		content: attr(data-text);
		color: #0d0e56;
		width: 100%;
		height: 150%;
		position: absolute;
		top: 1px;
		left: 2px;
		z-index: -1;
	}
}

.storytextblock {
	/* width: 900px; */
	/* margin-left: auto; */
	max-width: 900px;
	margin: 0 auto;
	grid-column: 5 / 11;
	grid-row: 1;
}

.feature-img-col {
	&.left-col {
		grid-column: 2 / 5;
		grid-row: 1;
		z-index: 1;
	}

	&.right-col {
		grid-column: 8 / 13;
		grid-row: 1;
	}
}

.feature-img {
	/* box-shadow: 0px 0px 10px 3px #fff; */
	max-width: 100%;
}

/*
.feature-img-col {
	position:absolute;
	top: 0;
}
*/

@media screen and (max-width: 768px) {
	.feature-img-col.left-col {
		grid-column: 2 / 5;
	}

	#story {
		.wrapper {
			padding: 0em;
		}

		.text-block {
			padding: 1em;
		}
	}

	.feature-img-col {
		flex-shrink: 1;
		width: 100%;
	}

	#story {
		text-align: center;
	}

	.feature-text-col h2 {
		text-align: center;
		padding-top: 1em;
	}
}
