@keyframes floatUpDown {
    0% {
        transform: translateY(0);
    }
    100% {
        transform: translateY(-2vh);
    }
}

#main {
	display: flex;
	justify-content: center;
	align-items: center;
	width: 100%;
	height: 100vh;
	position: relative;
	overflow: hidden;
	background-size: cover;
	background-position: top right;
	background-attachment: fixed;

	#title {
		display: flex;
		justify-content: center;
		align-items: center;
		flex-direction: column;
		color: #fff;
		text-align: center;
		text-shadow: 0 2px 3px rgba(0, 0, 0, 0.4);

		#title-logo {
			top: 50%;
			max-width: 100%;
			max-height: 100vh;
			margin: auto;
			filter: contrast(105%);
			animation: floatUpDown 3s ease-in-out infinite alternate;
		}
	}

	#button-container {
		position: absolute;
		top: 80%;
		display: inline-flex;
	}

	#scrollNotice {
		font-family: "Tsukimi Rounded SemiBold", sans-serif;
		width: 100%;
		position: absolute;
		bottom: 1%;
		font-size: 18pt;

		.fa-solid {
			font-size: 20pt;
			vertical-align: middle;
			margin: 0px 10px !important;
		}
	}
}

.esrbContainer {
	bottom: 1%;
	height: 10%;
	right: 1%;
	margin: 0 auto;
	position: absolute;
	z-index: 2;
}

.esrbHero {
	height: 100%;
}

@media screen and (max-width: 768px) {
	#main {
		#title-logo {
			animation: floatUpDown 3s ease-in-out infinite alternate; /* Adjust timing and animation as needed */
		}

		#button-container {
			position: absolute;
			top: 65%;
			display: inline;

			.button {
				min-width: 75px;
				padding: 12px 12px !important;
				font-size: 15px !important;
				margin: 5px;
				
				.fa-brands, .fa-solid {
					margin-right: 5px !important;
					font-size: 17px;
				}
			}
		}

		.esrbContainer {
			display: none;
		}

		#scrollNotice {
			font-size: 15pt;
	
			.fa-solid {
				font-size: 15pt;
				vertical-align: middle;
				margin-right: 7px;
			}
		}
	}
}
