body {
  cursor: url("cursor.png"), auto;
  background-image: url("img/HellBG.jpg");
  background-repeat: no-repeat;
  background-attachment: fixed;
  background-size: cover;
  background-position: center;
  margin: 0;
  font-size: 18px;
}

@media screen and (max-width: 1024px) {
  body {
    font-size: 14px;
  }
}
@media screen and (max-width: 768px) {
  body {
    font-size: 12px;
  }
}
nav {
  font-family: "Tsukimi Rounded Regular", sans-serif;
  font-style: italic;
}

.back-overlay {
  position: fixed;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 0;
  background: linear-gradient(to bottom, rgba(0, 0, 0, 0) 0%, rgba(0, 0, 0, 0.5) 100%);
  backdrop-filter: contrast(1.25);
}

.button {
  font-family: "Tsukimi Rounded SemiBold", sans-serif;
  box-shadow: 0 3px 6px rgba(0, 0, 0, 0.25), inset 0 -4px 0 rgba(0, 0, 0, 0.2);
  min-width: 150px;
  display: inline-flex !important;
  background-color: #ffabab;
  padding: 15px 20px !important;
  text-align: center !important;
  border-radius: 15px;
  color: #fff;
  cursor: pointer;
  overflow-wrap: break-word;
  vertical-align: middle;
  border: 0 none #fff !important;
  text-decoration: none;
  text-shadow: none;
  font-size: 20px !important;
  margin: 10px;
  transition: all linear 0.1s;
}
.button .fa-brands, .button .fa-solid {
  margin: 0px 5px !important;
  font-size: 22px;
  vertical-align: middle;
}
.button.kofi {
  color: #fff;
  background-color: #29abe0;
}
.button.reddit {
  color: #ff4500;
  background-color: #fff;
}
.button:hover {
  border-radius: 10px;
  filter: brightness(1.15);
}
.button:active {
  box-shadow: none;
}

img,
object {
  max-width: 100%;
}

h1,
h2,
h3,
h4,
h5 {
  text-align: center;
  margin: 0;
}

ul {
  list-style: none;
  padding: 0;
  margin: 0;
}

.hellbg {
  padding-top: 50px;
  background: linear-gradient(to bottom, rgba(0, 0, 0, 0) 0%, rgba(0, 0, 0, 0.5) 100%), url("hellish-pattern.png");
  background-position: fixed, relative;
  background-repeat: no-repeat, repeat;
  z-index: -1;
  -webkit-mask-image: linear-gradient(to bottom, rgba(0, 0, 0, 0) 0%, rgb(14, 4, 4) 100px);
  mask-image: linear-gradient(to bottom, rgba(0, 0, 0, 0) 0%, rgb(14, 4, 4) 100px);
}

.text-block {
  padding: 0.5em 1em;
}

.content-row {
  width: 100%;
  position: relative;
  /* display: flex;
  flex-wrap: nowrap;
  align-items: flex-start; */
}

.left-col,
.right-col {
  box-sizing: border-box;
  flex-basis: 50%;
  flex-shrink: 1;
  flex-grow: 1;
}

.wrapper {
  width: 1920px;
  max-width: 100%;
  box-sizing: border-box;
  margin: auto;
  position: relative;
  z-index: 1;
}

.line-break {
  display: block;
  margin: 1em;
}

button {
  border: none;
  font-family: inherit;
  font-size: inherit;
  text-transform: uppercase;
  padding: 0.5em 1em;
  cursor: pointer;
}

.cta-button-primary {
  background-color: #fdd700;
}

.cta-button-secondary {
  background-color: #fff;
}

/*Loader*/
.loader h5 {
  font-family: "Fronds Getturing", sans-serif;
  font-size: 3em;
  color: #ffffff;
  position: relative;
  z-index: 1;
  align-items: center;
  justify-content: center;
  padding: 1em 0;
}

.section-header {
  font-family: "Fronds Getturing", sans-serif;
  font-size: 3em;
  color: #ffffff;
  position: relative;
  z-index: 1;
  align-items: center;
  justify-content: center;
  padding: 1em 0 0.3em 0;
}

.loader {
  background: rgb(3, 0, 0);
  height: 100%;
  position: fixed;
  top: 0;
  width: 100%;
  z-index: 99999;
}

.loaderContainer {
  display: flex;
  flex-wrap: wrap;
  height: 100%;
  justify-content: center;
  align-items: center;
  text-align: center;
}

.section-header {
  display: flex;
  align-items: center;
  justify-content: center;
}

.section-header.featuredImg::before,
.section-header.succubi::before {
  position: absolute;
  z-index: -1;
  width: 150%;
  top: 40%;
}
@charset "UTF-8";
#story {
  position: relative;
  padding: 1em;
  color: #fff;
  display: flex;
  text-align: justify;
  flex-wrap: wrap;
  justify-content: center;
  font-family: "Tsukimi Rounded Medium", sans-serif;
  font-style: normal;
  margin-left: auto;
  margin-right: auto;
  max-width: 1600px;
  font-size: clamp(13px, 2vw, 40px);
}
#story::before {
  content: "";
  background-attachment: fixed;
  background-size: cover;
  z-index: -2;
}
#story::after {
  content: "";
  /*
  background: 
  linear-gradient(to bottom, rgba(0,0,0,1) 0%, rgba(0,0,0,0) 15%),
  linear-gradient(to top, rgba(0,0,0,1) 0%, rgba(0,0,0,0) 15%);
  */
  z-index: -1;
}
#story::before, #story::after {
  background-position: center;
  background-repeat: no-repeat;
  width: 100%;
  height: 100%;
  position: absolute;
  left: 0;
  top: 0;
  right: 0;
  margin: auto;
}
#story .content-row {
  display: grid;
  grid-template-columns: repeat(12, 1fr);
}

#story.section-header {
  width: 100%;
}

/*
#story h2 {
	font-size: 1.5em;
	letter-spacing: .1em;
}
*/
#story h5 {
  font-family: "Tsukimi Rounded Light", sans-serif;
  color: rgba(216, 209, 204, 0.4);
}

.feature-text-col {
  letter-spacing: 0.05em;
}
.feature-text-col h2 {
  text-align: left;
}

#story ul li::before {
  content: "• ";
}
#story li {
  margin-bottom: 0.5em;
}
#story .text-block {
  background-color: #0d0e56;
  padding: clamp(50px, 5vw, 100px);
  /* padding: 2em; */
  max-width: 100%;
  border: 5px solid #05fdfd;
  z-index: -1;
  position: relative;
}

.storyrow {
  margin-top: 150px;
  margin-bottom: 5%;
}

.textbox-header {
  font-family: ivypresto-display, serif;
  font-style: normal;
  color: #05fdfd;
  font-size: clamp(25px, 5vw, 125px);
  position: relative;
  z-index: 1;
  top: 0px;
}
.textbox-header::after {
  content: attr(data-text);
  color: #0d0e56;
  width: 100%;
  height: 150%;
  position: absolute;
  top: 1px;
  left: 2px;
  z-index: -1;
}

.storytextblock {
  /* width: 900px; */
  /* margin-left: auto; */
  max-width: 900px;
  margin: 0 auto;
  grid-column: 5/11;
  grid-row: 1;
}

.feature-img-col.left-col {
  grid-column: 2/5;
  grid-row: 1;
  z-index: 1;
}
.feature-img-col.right-col {
  grid-column: 8/13;
  grid-row: 1;
}

.feature-img {
  /* box-shadow: 0px 0px 10px 3px #fff; */
  max-width: 100%;
}

/*
.feature-img-col {
	position:absolute;
	top: 0;
}
*/
@media screen and (max-width: 768px) {
  .feature-img-col.left-col {
    grid-column: 2/5;
  }
  #story .wrapper {
    padding: 0em;
  }
  #story .text-block {
    padding: 1em;
  }
  .feature-img-col {
    flex-shrink: 1;
    width: 100%;
  }
  #story {
    text-align: center;
  }
  .feature-text-col h2 {
    text-align: center;
    padding-top: 1em;
  }
}
@keyframes floatUpDown {
  0% {
    transform: translateY(0);
  }
  100% {
    transform: translateY(-2vh);
  }
}
#main {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100vh;
  position: relative;
  overflow: hidden;
  background-size: cover;
  background-position: top right;
  background-attachment: fixed;
}
#main #title {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  color: #fff;
  text-align: center;
  text-shadow: 0 2px 3px rgba(0, 0, 0, 0.4);
}
#main #title #title-logo {
  top: 50%;
  max-width: 100%;
  max-height: 100vh;
  margin: auto;
  filter: contrast(105%);
  animation: floatUpDown 3s ease-in-out infinite alternate;
}
#main #button-container {
  position: absolute;
  top: 80%;
  display: inline-flex;
}
#main #scrollNotice {
  font-family: "Tsukimi Rounded SemiBold", sans-serif;
  width: 100%;
  position: absolute;
  bottom: 1%;
  font-size: 18pt;
}
#main #scrollNotice .fa-solid {
  font-size: 20pt;
  vertical-align: middle;
  margin: 0px 10px !important;
}

.esrbContainer {
  bottom: 1%;
  height: 10%;
  right: 1%;
  margin: 0 auto;
  position: absolute;
  z-index: 2;
}

.esrbHero {
  height: 100%;
}

@media screen and (max-width: 768px) {
  #main #title-logo {
    animation: floatUpDown 3s ease-in-out infinite alternate; /* Adjust timing and animation as needed */
  }
  #main #button-container {
    position: absolute;
    top: 65%;
    display: inline;
  }
  #main #button-container .button {
    min-width: 75px;
    padding: 12px 12px !important;
    font-size: 15px !important;
    margin: 5px;
  }
  #main #button-container .button .fa-brands, #main #button-container .button .fa-solid {
    margin-right: 5px !important;
    font-size: 17px;
  }
  #main .esrbContainer {
    display: none;
  }
  #main #scrollNotice {
    font-size: 15pt;
  }
  #main #scrollNotice .fa-solid {
    font-size: 15pt;
    vertical-align: middle;
    margin-right: 7px;
  }
}
#featuredImg .section-header,
#succubi .section-header {
  font-size: clamp(40px, 5vw, 100px);
}

#featuredImg .section-header {
  transform: translateX(10%) rotate(5deg);
}

#featuredImg img {
  display: block;
  margin-left: auto;
  margin-right: auto;
  border-radius: 1rem;
  width: 900px;
  max-width: 93%;
}

.prevArrow,
.nextArrow {
  cursor: pointer;
  position: absolute;
  top: 50%;
  transform: translate(0, -50%);
  width: 100px;
}

.nextArrow {
  right: 0;
}

.prevArrow {
  left: 0;
  z-index: 1;
}

.carouselImg {
  width: 100%;
}

.playButton {
  left: 50%;
  /* opacity: .75; */
  position: absolute;
  top: 50%;
  transform: translate(-50%, -50%);
  width: clamp(50px, 20vw, 418px);
  pointer-events: none;
}

@media screen and (min-width: 640px) {
  .nextArrow {
    right: -15px;
  }
  .prevArrow {
    left: -15px;
  }
}
.splide__arrow--prev {
  left: -8vw;
}

.splide__arrow--next {
  right: -8vw;
}

.splide__arrow {
  background: transparent;
  height: auto;
  opacity: 0.5;
  max-width: 10%;
}
nav {
  position: relative;
  left: 0;
  width: 100%;
  display: flex;
  justify-content: center;
  font-family: "Vtks Rash", sans-serif;
  font-size: 1.15em;
  text-transform: uppercase;
  z-index: 100;
}
nav .wrapper {
  position: absolute;
  width: 100%;
}

.nav-container {
  display: grid;
  grid-template-columns: repeat(12, 1fr);
}

.signature {
  height: 60px;
  grid-column: 1;
  padding: 7px;
}

.menu {
  grid-column: 2/12;
  display: grid;
  grid-template-columns: repeat(10, 1fr);
  align-items: center;
}
.menu ul li {
  display: inline-block;
}

.page-nav-links {
  grid-column: 1/8;
}
.page-nav-links li {
  padding: 0 1em;
}

.cta-nav-links {
  grid-column: 9/11;
  text-align: right;
}

.signature img.logo {
  height: 100%;
}

nav li {
  margin-bottom: 0;
}

.navStick {
  backdrop-filter: blur(5px);
  height: 60px;
  position: fixed;
  top: -80px;
  width: 100%;
  z-index: 2;
  -webkit-mask-image: linear-gradient(to bottom, rgb(0, 0, 0) 70%, rgba(0, 0, 0, 0) 100%);
  mask-image: linear-gradient(to bottom, rgb(0, 0, 0) 70%, rgba(0, 0, 0, 0) 100%);
}

.hamburger.is-active .hamburger-inner,
.hamburger.is-active .hamburger-inner::before,
.hamburger.is-active .hamburger-inner::after {
  background-color: white;
}

.hamburger-inner,
.hamburger-inner::before,
.hamburger-inner::after {
  background-color: white;
}

button.hamburger {
  display: none;
}

.purchaseNav {
  font-size: 1.1em;
}

@media screen and (max-width: 768px) {
  button.hamburger {
    display: inline-block;
    z-index: 30;
    position: relative;
  }
  .showMenu {
    display: block;
    transform: translate(0, 0);
  }
  .collapseMenu {
    display: block;
    transform: translate(0, -100%);
  }
  .signature {
    display: none;
  }
  .menu {
    display: block;
    background-color: rgba(17, 8, 9, 0.85);
    /* display: none; */
    height: 100vh !important;
    position: absolute;
    text-align: center;
    transition: transform 0.3s ease;
    width: 100%;
    z-index: 10;
    top: 0;
  }
  .menu ul li {
    display: block;
  }
  .page-nav-links {
    color: #fff;
    margin: 60px auto 25px;
    position: relative;
    width: 100%;
  }
  .page-nav-links li {
    background-color: transparent;
    display: block;
    text-align: center;
    text-transform: uppercase;
    padding: 0.8em;
    font-size: 2em;
  }
  .cta-nav-links {
    width: 75%;
    margin-left: auto;
    margin-right: auto;
    text-align: center;
  }
  .cta-nav-links li {
    margin: 0.5em;
  }
  .hamMenuTitle {
    color: #fff;
    display: block;
    font-size: 26px;
    font-style: italic;
    position: absolute;
    right: 65px;
    text-transform: uppercase;
    top: 50%;
    transform: translate(0, -50%);
    z-index: 20;
  }
}
