#succubi {
  /* background: #0D0E56; */
  position: relative;
  font-family: "Tsukimi Rounded Regular", sans-serif;
  color: #fff;
  text-align: justify;
  text-justify: auto;
  font-size: clamp(10px, 2vw, 18px);
  text-shadow: -2px 2px 0 #000, 2px 2px 0 #000, 2px -2px 0 #000, -2px -2px 0 #000;
}
#succubi h2 {
  text-align: left;
  padding-left: 10%;
}

/* .succubi-bio {
    // width: 50%;
} */
.succubus-bg {
  position: absolute;
  width: 100%;
  height: 100%;
  left: 0;
  bottom: 0;
}

.succubus-portrait {
  position: absolute;
  width: 100%;
  height: 100%;
  bottom: 0;
  padding-bottom: 7rem;
}

