.succubus-slide {
  overflow: hidden;
}

.succubus-splide,
.succubus-splide .splide__track {
  height: 100%;
}

.succubus-portrait {
  z-index: -1;
  /* margin-top: 5em; */
  display: grid;
  align-items: center;
  justify-content: space-evenly;
}

.succubus-portrait img {
  position: absolute;
  right: 0;
  bottom: 0;
  /* width: 50%; */
  max-height: 100%;
  max-width: 100%;
  height: auto;
  object-fit: contain;
}

#succubi .succubiSlide {
  background: url("img/mc_talk.png") center bottom/contain no-repeat;
  position: relative;
  padding-bottom: 10rem;
  /*
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  */
}

@media screen and (min-width: 1025px) {
  .succubus-bg {
    height: 100%;
  }
  #succubi .wrapper {
    margin-top: 3em;
  }
  #succubi .section-header {
    position: relative;
    transform: translateX(-10%) rotate(-15deg);
  }
  /* #succubi .succubiSlide {
  	clip-path: polygon(0 0, 100% 0, 100% 79%, 0 99%);
  } */
  #succubi .succubus-splide {
    width: 600px;
    margin: auto;
    transform: translateX(13.5%);
  }
  .succubi-bio {
    position: relative;
    bottom: 0;
    left: 50%;
    transform: translateX(-92%) translateY(-5%);
    width: 325px;
    max-width: 100%;
  }
}
#succubi .wrapper {
  height: 400px;
  width: 960px;
}

.succubus-slide-arrows {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}

@media screen and (max-width: 1024px) {
  .succubus-bg {
    height: 0;
  }
  .succubus-slide-arrows .prevArrow {
    max-width: 5%;
    left: 1em;
  }
  .succubus-slide-arrows .nextArrow {
