#featuredImg .section-header,
#succubi .section-header {
	font-size: clamp(40px, 5vw, 100px);
}

#featuredImg .section-header {
	transform: translateX(10%) rotate(5deg);
}

#featuredImg img {
    display: block;
	margin-left: auto;
	margin-right: auto;
	border-radius: 1rem;
	width: 900px;
	max-width: 93%;
}

.prevArrow, 
.nextArrow {
	cursor: pointer;
    position: absolute;
	top: 50%;
	transform: translate(0, -50%);
	width: 100px;
}

.nextArrow {
	right: 0;
}

.prevArrow {
	left: 0;
	z-index: 1;
}

.carouselImg {
	width: 100%;
}

.playButton {
	left: 50%;
	/* opacity: .75; */
	position: absolute;
	top: 50%;
	transform: translate(-50%, -50%);
	width: clamp(50px, 20vw, 418px);
	pointer-events: none;
}

@media screen and (min-width: 640px) {
	.nextArrow {
		right: -15px;
	}
	.prevArrow {
		left: -15px;
	}
}

.splide__arrow--prev {
    // left: clamp(1em, 5vw, 40em);
	left: -8vw;
}
.splide__arrow--next {
    // right: clamp(1em, 5vw, 40em);
	right: -8vw;
}
.splide__arrow {
    background: transparent;
	height:auto;
    opacity: 0.5;
	max-width: 10%;
}