nav {
	position: relative;
	left: 0;
	width: 100%;
	display: flex;
	justify-content: center;
	font-family: "Vtks Rash", sans-serif;
	font-size: 1.15em;
	text-transform: uppercase;
	z-index: 100;

	.wrapper {
		position: absolute;
		width: 100%;
	}
}

.nav-container {
	display: grid;
	grid-template-columns: repeat(12, 1fr);
}

.signature {
	height: 60px;
	grid-column: 1;
	padding: 7px;
}

.menu {
	grid-column: 2/12;
	display: grid;
	grid-template-columns: repeat(10, 1fr);
	align-items: center;

	ul li {
		display: inline-block;
	}
}

.page-nav-links {
	grid-column: 1/8;

	li {
		padding: 0 1em;
	}
}

.cta-nav-links {
	grid-column: 9/11;
	text-align: right;
}

.signature img.logo {
	height: 100%;
}

nav li {
	margin-bottom: 0;
}

.navStick {
	backdrop-filter: blur(5px);

	height: 60px;
	position: fixed;
	top: -80px;
	width: 100%;
	z-index: 2;

	-webkit-mask-image: linear-gradient(to bottom,
		rgb(0, 0, 0, 1) 70%,
		rgb(0, 0, 0, 0) 100%);
	mask-image: linear-gradient(to bottom,
		rgb(0, 0, 0, 1) 70%,
		rgb(0, 0, 0, 0) 100%);
}

.hamburger.is-active .hamburger-inner,
.hamburger.is-active .hamburger-inner::before,
.hamburger.is-active .hamburger-inner::after {
	background-color: white;
}
.hamburger-inner,
.hamburger-inner::before,
.hamburger-inner::after {
	background-color: white;
}

button.hamburger {
	display: none;
}

.purchaseNav {
	font-size: 1.1em;
}

@media screen and (max-width: 768px) {
	button.hamburger {
		display: inline-block;
		z-index: 30;
		position: relative;
	}

	.showMenu {
		display: block;
		transform: translate(0, 0);
	}

	.collapseMenu {
		display: block;
		transform: translate(0, -100%);
	}

	.signature {
		display: none;
	}

	.menu {
		display: block;

		ul li {
			display: block;
		}

		background-color: rgba(17, 8, 9, 0.85);

		/* display: none; */
		height: 100vh !important;
		position: absolute;
		text-align: center;
		transition: transform 0.3s ease;
		width: 100%;
		z-index: 10;
		top: 0;
	}

	.page-nav-links {
		color: #fff;
		margin: 60px auto 25px;
		position: relative;
		width: 100%;

		li {
			background-color: transparent;
			// border-bottom: 2px solid rgba(255, 255, 255, 0.75);
			display: block;
			text-align: center;
			text-transform: uppercase;
			padding: 0.8em;
			font-size: 2em;
		}
	}

	.cta-nav-links {
		width: 75%;
		margin-left: auto;
		margin-right: auto;
		text-align: center;

		li {
			margin: 0.5em;
		}
	}

	.hamMenuTitle {
		color: #fff;
		display: block;
		font-size: 26px;
		font-style: italic;
		position: absolute;
		right: 65px;
		text-transform: uppercase;
		top: 50%;
		transform: translate(0, -50%);
		z-index: 20;
	}
}