@font-face {
	font-family: "Tsukimi Rounded Light";
	src: url('fonts/TsukimiRounded-Light.woff2') format('woff2'),
	url('fonts/TsukimiRounded-Light.woff') format('woff');
}

@font-face {
	font-family: "Tsukimi Rounded Regular";
	src: url('fonts/TsukimiRounded-Regular.woff2') format('woff2'),
	url('fonts/TsukimiRounded-Regular.woff') format('woff');
}

@font-face {
	font-family: "Tsukimi Rounded Medium";
	src: url('fonts/TsukimiRounded-Medium.woff2') format('woff2'),
	url('fonts/TsukimiRounded-Medium.woff') format('woff');
}

@font-face {
	font-family: "Tsukimi Rounded SemiBold";
	src: url('fonts/TsukimiRounded-SemiBold.woff2') format('woff2'),
	url('fonts/TsukimiRounded-SemiBond.woff') format('woff');
}

@font-face {
	font-family: "Fronds Getturing";
	src: url('fonts/FrondsGetturing.woff2') format('woff2'),
	url('fonts/FrondsGetturing.woff') format('woff');
}

@font-face {
	font-family: "Vtks Rash";
	src: url('fonts/Vtks Rash.woff2') format('woff2'),
	url('fonts/Vtks Rash.woff') format('woff');
}

@font-face {
	font-family: "Fusha Brush";
	src: url('fonts/Fusha Brush.woff2') format('woff2'),
	url('fonts/Fusha Brush.woff') format('woff');
}

body {
	cursor: url('cursor.png'), auto;

	background-image: url("img/HellBG.jpg");
	background-repeat: no-repeat;
	background-attachment: fixed;
	background-size: cover;
	background-position: center;
	margin: 0;
	font-size: 18px;
}

@media screen and (max-width: 1024px) {
	body {
		font-size: 14px;
	}
}

@media screen and (max-width: 768px) {
	body {
		font-size: 12px;
	}
}

nav {
	font-family: "Tsukimi Rounded Regular", sans-serif;
	font-style: italic;
}

.back-overlay {
	position: fixed;
	bottom: 0;
	left: 0;
	width: 100%;
	height: 100%;
	z-index: 0;
	background: linear-gradient(to bottom,
			rgba(0, 0, 0, 0) 0%,
			rgba(0, 0, 0, 0.5) 100%);
	backdrop-filter: contrast(1.25);
}

.button {
	font-family: "Tsukimi Rounded SemiBold", sans-serif;
	box-shadow: 0 3px 6px rgba(0,0,0,0.25), inset 0 -4px 0 rgba(0,0,0,0.2);
	min-width: 150px;
	display: inline-flex !important;
	background-color: #ffabab;
	padding: 15px 20px !important;
	text-align: center !important;
	border-radius: 15px;
	color: #fff;
	cursor: pointer;
	overflow-wrap: break-word;
	vertical-align: middle;
	border: 0 none #fff !important;
	text-decoration: none;
	text-shadow: none;
	font-size: 20px !important;
	margin: 10px;
	transition: all linear 0.1s;
	
	.fa-brands, .fa-solid {
		margin: 0px 5px !important;
		font-size: 22px;
		vertical-align: middle;
	}

	&.kofi {
		color: #fff;
		background-color: #29abe0;
	}

	&.reddit {
		color: #ff4500;
		background-color: #fff;
	}

	&:hover {
		border-radius: 10px;
		filter: brightness(1.15); // Too lazy to configure the colors manually
	}

	&:active {
		box-shadow: none;
	}
}

img,
object {
	max-width: 100%;
}

h1,
h2,
h3,
h4,
h5 {
	text-align: center;
	margin: 0;
}

ul {
	list-style: none;
	padding: 0;
	margin: 0;
}

.hellbg {
	padding-top: 50px;
	background: linear-gradient(to bottom, rgba(0, 0, 0, 0) 0%, rgba(0, 0, 0, 0.5) 100%), url("hellish-pattern.png");
	background-position: fixed, relative;
	background-repeat: no-repeat, repeat;
	z-index: -1;

	-webkit-mask-image: linear-gradient(to bottom,
		rgba(0, 0, 0, 0) 0%,
		rgb(14, 4, 4, 1) 100px);
	mask-image: linear-gradient(to bottom,
		rgba(0, 0, 0, 0) 0%,
		rgb(14, 4, 4, 1) 100px);
}

.text-block {
	padding: 0.5em 1em;
}

.content-row {
	width: 100%;
	position: relative;

	/* display: flex;
	flex-wrap: nowrap;
	align-items: flex-start; */
}

.left-col,
.right-col {
	box-sizing: border-box;
	flex-basis: 50%;
	flex-shrink: 1;
	flex-grow: 1;
}

.wrapper {
	width: 1920px;
	max-width: 100%;
	box-sizing: border-box;
	margin: auto;
	position: relative;
	z-index: 1;
}

.line-break {
	display: block;
	margin: 1em;
}

button {
	border: none;
	font-family: inherit;
	font-size: inherit;
	text-transform: uppercase;
	padding: 0.5em 1em;
	cursor: pointer;
}

.cta-button-primary {
	background-color: #fdd700;
}

.cta-button-secondary {
	background-color: #fff;
}

/*Loader*/

.loader h5 {
	font-family: "Fronds Getturing", sans-serif;
	font-size: 3em;

	color: #ffffff;
	position: relative;
	z-index: 1;
    align-items: center;
    justify-content: center;
    padding: 1em 0;
}

.section-header {
	font-family: "Fronds Getturing", sans-serif;
	font-size: 3em;

	color: #ffffff;
	position: relative;
	z-index: 1;
    align-items: center;
    justify-content: center;
    padding: 1em 0 0.3em 0;
}

.loader {
	background: rgb(3, 0, 0);
	height: 100%;
	position: fixed;
    top: 0;
	width: 100%;  
    z-index: 99999;
}
.loaderContainer {
	display: flex;
	flex-wrap: wrap;
	height: 100%;
	justify-content: center;
	align-items: center;
	text-align: center;
}

.section-header {
	display: flex;
	align-items: center;
	justify-content: center;
}

.section-header.featuredImg::before,
.section-header.succubi::before {
	position: absolute;
	z-index: -1;
	width: 150%;
	top: 40%;
}